#pinned_project {
  color: var(--white);
  margin: 50px auto 100px auto;
  width: 90%;
}

.top {
  display: flex;
  margin: 0 auto;
  width: 100%;
  height: 70px;
  /*border: 1px solid white;*/
}

.top .left {
  display: flex;
  flex: 1;
  margin-left: 10px;
  /*border: 1px solid white;*/
}

.top .left .folder {
  width: 40px;
  color: rgb(0, 255, 255, 0.6);
}


.top .left .project_name {
  margin: 22px 0px 0px 8px;
  font-size: 22px;
  font-weight: 600;
  flex-wrap: wrap;
  height: fit-content;
  color: rgba(255, 255, 255, 0.7);
}

.movable_text {
  width: 65px;
  height: 40px;
  padding-top: 2px;
  font-size: 14px;
  transform: translateX(130px);
  transition: 700ms;
  color: rgb(0, 255, 255, 0.7);
  cursor: pointer;
  /*border: 1px solid white;*/
}

#pinned_project:hover .movable_text {
  transform: translateX(0px);
}

.top:hover .movable_text {
  transform: translateX(90px) !important;
}

.top .right {
  display: flex;
  margin-right: 15px;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  /*border: 1px solid white;*/
}

.top .right>* {
  color: rgb(0, 255, 255, 0.6);
  width: 24px;
  transition: 300ms;
}

.top .right>*:hover {
  color: rgb(0, 255, 255, 1);
  cursor: pointer;
}

.image_wrapper {
  margin: 0 auto;
  overflow: hidden;
  width: 100%;
  height: fit-content;
  transition: 400ms;
}

.image_wrapper:hover {
  /*box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;*/
}

.image_wrapper img {
  transition: 400ms;
  opacity: 0.8;
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  width: 100%;
}

.image_wrapper img:hover {
  opacity: 1;
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}

.about {
  padding: 15px 3px 10px 3px;
  border-radius: 6px;
  font-size: 14px;
}

.preview {
  cursor: pointer;
  transition: 400ms;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 10px;
  color: rgb(0, 255, 255, 0.6);
  /*border: 1px solid white;*/
}

.preview:hover {
  color: rgb(0, 255, 255, 0.7);
}

.preview:hover .image_wrapper img {
  opacity: 1;
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}

.preview .tech {
  padding: 0px 3px 0px 3px;
  flex-wrap: wrap;
  display: flex;
  width: 90%;
  font-size: 12px;
  font-weight: 200;
  color: rgba(255, 255, 255, 1);
}

.preview .tech>div {
  margin: 10px 10px 0px 0px;
  padding: 4px 10px 4px 10px;
  width: fit-content;
  height: fit-content;
  border-radius: 3px;
  border: 1px solid rgb(0, 255, 255, 0.2);
}

@keyframes glow {}